import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NumberDirective, FloatNumberDirective } from './directive/number.directive';
import { PricePipe, CardSizeSortForm, CardSizeFullForm, userLicensePipe, TireNamePipe } from './pipe/status.pipe';
import { RouterBackDirective } from './directive/router-back.directive';

const components = [
  NumberDirective,
  FloatNumberDirective,
  PricePipe, TireNamePipe,
  CardSizeSortForm,
  CardSizeFullForm,
  userLicensePipe,
  RouterBackDirective
]
@NgModule({
  declarations: components,
  imports: [CommonModule],
  exports: components
})
export class SharedModule { }
